.project-heading {
  text-align: center;
  padding: 4rem 0 2rem 0;
}

.project-container {
  max-width: 1140px;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 40px;
  padding-bottom: 10rem;
}

.project-card {
  background: #1a1919;
  padding: 1.2rem 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.project-card img {
  width: 100%;
}

.project-title {
  margin-top: 1rem;
  color: white;
}

.pro-content {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.pro-details {
  margin-top: 1rem;
  margin-bottom: 4rem; /* Add margin-bottom to create space between text and buttons */
}

.pro-btns {
  margin-top: auto;
}
